import NextAuth from "next-auth";
import Google from "next-auth/providers/google";
import Credentials from "next-auth/providers/credentials";
import type { NextAuthConfig } from "next-auth";
import { nanoid } from "nanoid";

// Mock user store for development (since we don't have a real DB connected yet)
// In production, this would be replaced by D1 adapter queries
const users = [
    {
        id: "1",
        name: "Admin User",
        email: "admin@intru.in",
        password: "password123", // In real app, this would be hashed
        role: "admin",
    },
];

export const authConfig = {
    providers: [
        Google({
            clientId: process.env.GOOGLE_CLIENT_ID,
            clientSecret: process.env.GOOGLE_CLIENT_SECRET,
        }),
        Credentials({
            name: "Sign in with Email",
            credentials: {
                email: { label: "Email", type: "email" },
                password: { label: "Password", type: "password" },
            },
            async authorize(credentials) {
                if (!credentials?.email || !credentials?.password) return null;

                // Simulate DB lookup
                const user = users.find((u) => u.email === credentials.email);

                if (user && user.password === credentials.password) {
                    return {
                        id: user.id,
                        name: user.name,
                        email: user.email,
                        role: user.role,
                    };
                }
                return null;
            },
        }),
    ],
    pages: {
        signIn: "/auth/signin", // We'll implement a custom modal instead
    },
    callbacks: {
        async jwt({ token, user, account }) {
            if (user) {
                token.id = user.id;
                token.role = (user as any).role || "customer";
            }
            return token;
        },
        async session({ session, token }) {
            if (session.user && token) {
                (session.user as any).id = token.id;
                (session.user as any).role = token.role;
            }
            return session;
        },
        async signIn({ user, account, profile }) {
            // Log sign-in attempt here (placeholder for logging system)
            console.log(`User ${user.email} logged in via ${account?.provider}`);
            return true;
        },
    },
    session: {
        strategy: "jwt",
    },
    secret: process.env.NEXTAUTH_SECRET,
} satisfies NextAuthConfig;

export const { handlers, auth, signIn, signOut } = NextAuth(authConfig);
